.user-nav {
  list-style: none;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  margin: 0;
  padding-left: 0;
  border-top: 1px solid $color-lightgrey-300;


  @media (min-width: $tablet-width) {
    display: flex;
    border: none;
  }

  @media (min-width: $desktop-width) {
    order: 3;
  }
}

.user-nav__item {
  position: relative;
  border-bottom: 1px solid $color-lightgrey-300;

  @media (min-width: $tablet-width) {
    display: flex;
    border: none;
  }
}

.user-nav__icon {
  color: rgba($color-black-900, 0.2);
  position: absolute;
  top: 32px;
  left: calc(50% - 130px);

  @media (min-width: $tablet-width) {
    top: 36px;
    left: 33px;
  }
}

.user-nav__cart {
  max-width: 260px;
  padding-top: 29px;
  padding-bottom: 27px;
  padding-left: 46px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  color: $color-black-900;
  text-decoration: none;

  @media (min-width: $tablet-width) {
    border: none;
    max-width: 100%;
    padding-left: 82px;
    padding-top: 32px;
    padding-bottom: 0;
  }
}

.user-nav__cart::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";

  @media (min-width: $tablet-width) {
    display: none;
  }
}


.user-nav__search {
  max-width: 260px;
  padding-top: 26px;
  padding-bottom: 26px;
  padding-left: 46px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  color: $color-black-900;
  text-decoration: none;

  @media (min-width: $tablet-width) {
    border: none;
    border-left: 1px solid $color-lightgrey-300;
    border-right: 1px solid $color-lightgrey-300;
    padding-right: 35px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.user-nav__search::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.user-nav__link-text {
  @media (min-width: $tablet-width) {
    position: absolute;
    opacity: 0;
  }
}

.user-nav__item:hover .user-nav__icon {
  color: $primary-color-200;
}

.user-nav__item:active .user-nav__icon {
  color: rgba($primary-color-200, 0.2);
}

.user-nav__cart:hover {
  color: rgba($color-black-900, 0.6);
}
